.details-header {
    font-weight: bold;
}

.details-container {
    margin: 1rem;
}

.details {
    color: #777;
    font-weight: bold;
    margin: .1rem 0 1rem 1.8rem;
}

.details-icon {
    margin-right: .5rem;
}

.edit-btn {
    width: 72px;
    background-color: #3dbbe6;
    color: white;
    margin-right: 1rem;
}

.edit-btn:hover {
    background: #049fd6;
    color: white;
}

.edit-input>input {
    padding: 6px 0 !important;
    margin-bottom: 1rem;
}